.btn {
    .box-sizing(border-box);
    display: inline-block;
    position: relative;
    margin: 0;
    font-weight: normal;
    text-align: left;
    cursor: pointer;
    border: 1px solid transparent;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: baseline;
    .btn-base-color();
    .btn-base-size();
    .user-select(none);

    &:focus,
    &.focus {
        outline: none;
    }

    &.focus {
        border-radius: @btn-border-radius;
        border: 1px solid #ffffff;
        .box-shadow(0 0 0 2px @border-focus-color);
    }

    &:active,
    &.active {
        .box-shadow(inset 0 2px 1px rgba(0,0,0,.2));
    }

    &.checked {
        .btn-checked();
        .box-shadow(inset 0 3px 2px -2px rgba(0, 0, 0, 0.33));
    }

    &.disabled,
    &[disabled],
    fieldset[disabled] & {
        cursor: default;
        pointer-events: none;
        .box-shadow(none);
        .btn-disabled();
    }
    & + .btn {
        margin-left: 2px;
    }
}

.btn {
    .icon:before {
        .box-sizing(border-box);
        line-height: 11px !important;
        padding-right: 5px;
  }
}

.btn-size-30 {
    .button-size(32px, 11px, @btn-padding-left, 11px);
}

.btn-middle {
    .button-size(38px, 13px, @btn-padding-left, 15px);
    & + .btn-middle {
        margin-left: 4px;
    }
}

.btn-size-40 {
    .button-size(42px, 15px, @btn-padding-left + 5, 16px, @btn-40-font-size);
}

.btn-big {
    .btn-size-40;
    & + .btn-big {
        margin-left: 5px;
    }
}


.btn-green {
    color: @text-color-white;
    background-color: @btn-green-bg;
    .linear-gradient(@btn-green-bg; @btn-green-bg-bottom);
    .border-color(@btn-green-border, @btn-green-border-bottom);

    &:hover,
    &.hover {
        background-color: @btn-green-bg-hover;
        .linear-gradient(@btn-green-bg-hover; @btn-green-bg-bottom-hover);
        .border-color(@btn-green-border-hover, @btn-green-border-bottom-hover);
    }
    &:active,
    &.active {
        background-color: @btn-green-bg-active;
        .linear-gradient(@btn-green-bg-active);
        .border-color(@btn-green-border-active);
    }
}

