.btn .loader {
    background: url('../img/loader.gif') center no-repeat;
}

.btn-group {
    .box-sizing(border-box);
    display: inline-block;
    font-size: 0;

    .btn {
        border-radius: 0;
    }

    .btn + .btn {
        border-left-width:0;
        margin-left: 0;
    }

    .btn:first-child {
        border-top-left-radius: @btn-border-radius;
        border-bottom-left-radius: @btn-border-radius;
    }

    .btn:last-child { // TODO: такая конструкция не работает в ie8, надо дописать fallback через класс .btn-last-child
        border-top-right-radius: @btn-border-radius;
        border-bottom-right-radius: @btn-border-radius;
    }

    .btn.btn-last-child {
        border-top-right-radius: @btn-border-radius;
        border-bottom-right-radius: @btn-border-radius;
    }
}

.btn {
    &.btn-dropdown:after {
        .btn-function-icon("\e04c");
    }
}

.btn {
    &.btn-modal:after {
        .btn-function-icon("\e019");
    }
}

.btn {
    &.btn-select:after {
        .btn-function-icon("\e014");
    }
}
