.cbox-base-color() {
    &:before{
        color: @text-color-base;
        background-color: @cbox-base-bg;
        .linear-gradient(@cbox-base-bg; @cbox-base-bg-bottom);
        .border-color(@cbox-base-border, @cbox-base-border-bottom);
    }

    &:hover:before,
    &.hover:before {
        background-color: @cbox-base-bg-hover;
        .linear-gradient(@cbox-base-bg-hover; @cbox-base-bg-bottom-hover);
        .border-color(@cbox-base-border-hover, @cbox-base-border-bottom-hover);
    }

    &:active:before,
    &.active:before {
        background-color: @cbox-base-bg-active;
        .linear-gradient(@cbox-base-bg-active);
        .border-color(@cbox-base-border-active);
    }
}

.cbox-disabled() {
    &:before {
        color: @text-color-disabled;
        background: @disabled-color !important;
        //.linear-gradient(@disabled-color);
        .border-color(@border-disabled-color);
    }

    &:hover:before,
    &.hover:before {
        background-color: @disabled-color;
        .linear-gradient(@disabled-color);
        .border-color(@border-disabled-color);
    }

    &:active:before,
    &.active:before {
        background-color: @disabled-color;
        .linear-gradient(@disabled-color);
        .border-color(@border-disabled-color);
    }
}

.cbox-checked() {
    &:before {
        color: @text-color-white;
        background-color: @cbox-base-bg-checked;
        .linear-gradient(@cbox-base-bg-checked);
        .border-color(@cbox-base-border-checked);
    }

    &:hover:before,
    &.hover:before {
    background-color: @cbox-base-bg-checked;
        .linear-gradient(@cbox-base-bg-checked);
        .border-color(@cbox-base-border-checked);
    }

    &:active:before,
    &.active:before {
        background-color: @cbox-base-bg-checked;
        .linear-gradient(@cbox-base-bg-checked);
        .border-color(@cbox-base-border-checked);
    }
}


.linear-gradient(@top) {
  .linear-gradient(@top; @top);
}
.linear-gradient(@top; @bottom) {
  background-image: -webkit-linear-gradient(top, @top, @bottom);
  background-image: -moz-linear-gradient(top, @top, @bottom);
  background-image: -ms-linear-gradient(top, @top, @bottom);
  background-image: linear-gradient(to bottom, @top, @bottom);
}

.border-color(@color) {
  .border-color(@color; @color);
}
.border-color(@top; @bottom) {
  border-top-color: @top;
  border-right-color: @top;
  border-left-color: @top;
  border-bottom-color: @bottom;
}

