/** @section Reset */

.calendar ol, .calendar ul { 
    list-style: none;
    margin: 0; 
    padding: 0;
}
.calendar li { 
    display: list-item;
    margin: 0;
    padding: 0;
}

/** @section Calendar */

.calendar {
    .box-sizing(border-box);
    display: none;
    position: absolute;
    width: 217px;
    height: 217px;
    line-height: 1em;
    font-family: "Segoe UI", Arial;
    font-size: 14px;
    text-align: left;
    z-index: 5;
    border:solid 1px;
    border-color: #d9d9d9;
    border-radius:1px;
    background:#fff;
    box-shadow:0 0 1px rgba(0,0,0,0.1);
    -webkit-user-select: none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}

    .calendar[class*='calendar_'],
    .calendar * {
        .box-sizing(border-box);
    }

.calendar_center {
    position: relative;
}

/** @section Calendar panel */

.calendar_panel {
    position: absolute;
    top: 1px;
    left: 0;
    right:0;
    padding: 10px 0 0 25px;
    height: 29px;
    .linear-gradient(#F4F4F4,#DFDFDF);
    border-bottom: 1px solid #999999;
    box-sizing:content-box;
}

.calendar__revert .calendar_panel {
    top: 175px;
    background-color: #f0f0f0;
    border-bottom: 0;
    border-top: 1px solid #bfbfbf;
}

.calendar_panel_shadow {
  position: absolute;
  left: -6px;
  top: 39px;
  width: 227px;
  height: 1px;
  box-shadow: 0 1px 7px rgba(0, 0, 0, 1);
  clip: rect(auto 221px 7px 6px);
}


.calendar__revert .calendar_panel_shadow {
    top: 0;
    box-shadow: 0 -1px 7px rgba(0, 0, 0, 1);
    clip: rect(-7px 221px auto 6px);
}


