/** @section Calendar selects */

.calendar_selectControl {
    position: relative;
    float: left;
    margin-right: 10px;
    height: 21px;
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.05) inset;
    border: solid 1px;
    border-color: rgba(0,0,0,0.3) rgba(0,0,0,0.15) rgba(0,0,0,0.15);
    border-radius:1px;
    background:#fff;
}

.calendar_selectControl_value {
    padding-left: 6px;
    height: 19px;
    line-height: 17px;
    font-size: 13px;
    cursor: pointer;
}

.calendar_monthSelect .calendar_selectControl_value {
    width: 80px;
}

.calendar_yearSelect .calendar_selectControl_value {
    width: 50px;
}

.calendar_monthSelect .calendar_selectControl_value:after,
.calendar_yearSelect .calendar_selectControl_value:after {
    content:"";
    display:block;
    border: solid 4px transparent;
    border-top:solid 4px #000;
    position: absolute;
    right: 3px;
    top: 8px;
}

.calendar_select_directions {
    position: absolute;
    left: 100%;
    top: 6px;
    margin-left: -12px;
    width: 5px;
    height: 9px;
    cursor: pointer;
}

/** @section Calendar select */

.calendar_select {
    display: none;
    position: absolute;
    left: 0;
    top: -100px;
    border: 1px solid #abadb3
}

.calendar_monthSelect .calendar_select {
    width: 88px;
}

.calendar_yearSelect .calendar_select {
    width: 52px;
}

