.calendar_select_viewPort {
    background-color: #fff;
}

.calendar_select__scroll .calendar_select_viewPort {
    position: relative;
    height: 200px; /* Temp */
    overflow: hidden;
}

.calendar_select_list {
    padding: 2px 0 4px !important;
}

.calendar_select__scroll .calendar_select_list {
    position: absolute;
    left: 0;
    padding: 0;
    width: 100%;
}

.calendar_select_list_item {
    display: block;
    padding-left: 5px;
    height: 19px;
    line-height: 19px;
    font-size: 13px;
    text-decoration: none;
    color: #000;
}

.calendar_select_list_item__selected {
    background-color: #ebebeb;
}

.calendar_select_list_item:hover {
    background-color: #E9E9E9;
}

.calendar_select_toStart,
.calendar_select_toEnd {
    display: none;
    height: 14px;
    line-height: 1px;
    font-size: 1px;
    cursor: pointer;
    position:relative;
    background:#fff;
}

.calendar_select_toStart:after,
.calendar_select_toEnd:after {
        content:"";
        display:block;
        border: solid 4px transparent;
        border-top:solid 4px #000;
        position: absolute;
        left:50%;
        top: 5px;
        margin-left:-4px;
}

.calendar_select_toStart:after {
    border-top-color:transparent;
    border-bottom-color:#000;
    top:1px;
}

.calendar_select__scroll .calendar_select_toStart,
.calendar_select__scroll .calendar_select_toEnd {
    display: block;
}

.calendar_select_toStart:hover,
.calendar_select_toEnd:hover {
    background: #ebebeb;
}


/** @section Days */
.calendar_days_viewPort {
    position: absolute;
    left: 0;
    top: 41px;
    width: 216px;
    height: 174px;
    overflow: hidden;
}

.calendar_days {
    position: absolute;
    left: 0;
    top: 0;
    width: 215px;
}

.calendar__revert .calendar_days_viewPort {
    top: 0;
}

.calendar_month {
    position: relative;
}

.calendar_month_title {
    position: absolute;
    left: 100%;
    top: 5px;
    margin-left: -36px;
    width: 36px;
    text-align: center;
    font-size: 0.857em;
    text-transform: lowercase;
}

.calendar_month__hold .calendar_month_title {
    display: none;
}

.calendar_monthHolder {
    display: none;
    position: absolute;
    left: 100%;
    top: 18px;
    margin-left: -36px;
    width: 36px;
    text-align: center;
    text-transform: lowercase;
    font-size: 0.857em;
}

.calendar_hideYearHolder .calendar_month_title_yearValue {
    display: none;
}

.calendar_ready {
    display: none;
}

.calendar_week {
    height: 25px;
}

.calendar_day {
    box-sizing:initial;
    float: left;
    width: 25px;
    height: 25px;
}

.calendar_day__gray {
    background-color: #f1f1f1;
}

.calendar_day__firstInMonth {
    -moz-border-radius: 3px 0 0 0;
    -webkit-border-radius: 3px 0 0 0;
    -khtml-border-radius: 3px 0 0 0;
    -ms-border-radius: 3px 0 0 0;
    border-radius: 3px 0 0 0;
}

.calendar_day__lastInMonth {
    -moz-border-radius: 0 0 3px 0;
    -webkit-border-radius: 0 0 3px 0;
    -khtml-border-radius: 0 0 3px 0;
    -ms-border-radius: 0 0 3px 0;
    border-radius: 0 0 3px 0;
}

.calendar_day a {
    display: block;
    box-sizing: initial;
    float: left;
    padding-top:5px;
    margin-left:1px;
    width: 24px;
    height: 19px;
    line-height: 13px;
    text-align: center;
    text-decoration: none;
    font-size: 11px;
    color: #000;
    border-radius:2px;
}

.ie-lt8 .calendar_day a {
    padding-top: 6px;
    height: 18px;
}

.calendar_day__weekend a {
    color: #ee474d;
}

.calendar_day__today a {
}

.calendar_day__last {
    padding-right: 40px;
}

.calendar_day a:hover,
.calendar_day__weekend a:hover,
.calendar_day__today a:hover,
.calendar_day__selected a {
    color: #fff;
    background: #3071A4;
}

.isDrag .calendar_day a:hover {
    color: #000 !important;
    background: none !important;
}

.isDrag .calendar_day__weekend a:hover {
    color: #ee474d !important;
}

.isDrag .calendar_day__today a:hover {
}
