.menu {
  .box-sizing(border-box);
  padding: 0;
  margin: 0;
  list-style-type: none;
  list-style-position: outside;
  list-style-image: none;
  font-size: @font-size-large;
  font-family: inherit;
  min-width: 100px;
  cursor: none;

  & .menu-item {
    .box-sizing(border-box);
    padding: 28px 0 28px 16%;
    margin: 0;
    position: relative;
    cursor: pointer;

    & .menu-item-image {
      .box-sizing(border-box);
      padding-left: 50px;
      display: inline-block;
    }

    &:active, &.active {
      background-image: url("@{img-path}tileMenu__pointer.png");
      background-repeat: no-repeat;
      background-position: right center;
      & .menu-pointer {
        display: block;
      }
    }
  }

  & .menu-separator {
    .box-sizing(border-box);
    height: 1px;
    margin: 0 22px;
    background-color: #646464;
  }
}

.menu-gray {
  .menu-variant(@text-color-menu; @menu-bg);
  & .menu-item {
    .tile-variant(@text-color-menu; @menu-bg; @menu-bg-hover; @menu-bg-active)
  }
}

.menu-blue {
  .menu-variant(@text-color-white; @tile-main-bg);
  & .menu-item {
    .tile-variant(@text-color-white; @tile-main-bg; @tile-main-bg-hover; @tile-main-bg-active)
  }
}


.icon-new {
  line-height: 24px;
  .icon-variant(0px);
}

.icon-fns {
  line-height: 24px;
  .icon-variant(-50px);
}

.icon-pfr {
  line-height: 32px;
  .icon-variant(-162px);
}

.icon-rosstat {
  line-height: 31px;
  .icon-variant(-98px);
}

.icon-fss {
  line-height: 25px;
  .icon-variant(-228px);
}

.icon-rar {
  line-height: 39px;
  .icon-variant(-279px);
}

.icon-rpn {
  line-height: 29px;
  .icon-variant(-357px);
}

& .active {
  .icon-new {
    .icon-variant(-25px);
  }

  .icon-fns {
    .icon-variant(-74px);
  }

  .icon-pfr {
    .icon-variant(-195px);
  }

  .icon-rosstat {
    .icon-variant(-130px);
  }

  .icon-fss {
    .icon-variant(-254px);
  }

  .icon-rar {
    .icon-variant(-318px);
  }

  .icon-rpn {
    .icon-variant(-387px);
  }
}
