.field {
  .box-sizing(border-box);
  display: inline-block;
  font-weight: normal;
  outline: none;
  border: 1px solid lighten(#000, 85%);
  border: 1px solid rgba(0,0,0,.15);
  border-top-color: lighten(#000, 70%);
  border-top-color: rgba(0,0,0,.3);
  vertical-align: baseline;
  .box-shadow(inset 0 1px 0 0 rgba(0,0,0,.05));
  .field-base-size();

  &:focus,
  &.focus {
    .colored-border(@border-focus-color);
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: default;
    pointer-events: none;
    color: @text-color-disabled;
    background-color: @disabled-color;
    border: 1px solid @border-disabled-color;
    .box-shadow(none);
  }

  &.width120 {
    width: 120px;
  }
}

.field {
  &.field-digit {
    text-align: right;
    width: 110px;
  }
}

.field {
  &.field-green {
    .colored-border(@green-color);
  }

  &.field-yellow {
    .colored-border(@yellow-color);
  }

  &.field-red {
    .colored-border(@red-color);
  }
}

.field {
  &.field-date {
    width: 95px;
  }
}

.field-size-30 {
  .field-size(32px, 4px, 8px, 5px, @font-size-base)
}

.field-group {
  .box-sizing(border-box);
  display: inline-block;
  font-size: 0;

  .btn {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    margin-left: -1px;
    vertical-align: top;
    top: 0;
  }
}
