h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: @font-family-base;
  font-weight: normal;
  line-height: 1;
  color: inherit;
  padding-bottom: @line-height-fixed / 2;
  margin: 0;
}

h1, h2, h3,
.h1, .h2, .h3 {
  padding-top: @line-height-fixed;
}

h4, h5, h6,
.h4, .h5, .h6 {
  padding-top: @line-height-fixed / 2;
}

h1, .h1 { font-size: @font-size-h1; font-weight: 200; line-height: 50px;}
h2, .h2 { font-size: @font-size-h2; font-weight: 300; line-height: 40px;}
h3, .h3 { font-size: @font-size-h3; line-height: 30px;}
h4, .h4, 
h5, .h5, 
h6, .h6 { font-size: @font-size-h4; line-height: 25px;}

p {
  margin: 0 0 @margin-paragraph;
}

ul, ol {
  font-size: @font-size-list;
  margin-top: 0;
  margin-bottom: (@line-height-fixed / 2);
  ul, ol {
    margin-bottom: 0;
  }
}
