.table {
    box-sizing: border-box;
    display: inline-block;
    font-style: normal !important;
    font-weight: normal !important;
    width: 100%;
    max-width: 100%;

    > thead, > tbody, > tfoot {
        > tr {
            > th, > td {
                padding: 10px 15px;
                line-height: @line-height-fixed;
                vertical-align: top;
                text-align: left;
                border-top: 1px solid @table-border;
            }
        }
    }

    > caption + thead, > thead:first-child {
        > tr:first-child {
            > th, > td {
                border-top: 0;
            }
        }
    }

    > thead {
        > tr {
            > th, > td {
                color: @text-color-light;
                font-weight: normal !important;
                font-size: @font-size-small;
            }
        }
    }

    > tbody {
        > tr {
            &:hover,
            &.hover {
                background-color: @table-bg-hover;
            }
        }
    }

    > tfoot {
        font-weight: bold !important;
    }

}

.table-right {
    > thead, > tbody, > tfoot {
        > tr {
            > th, > td {
                text-align: right;

                &:first-child {
                    text-align: left;
                }
            }
        }
    }
}
