.btn-base-size() {
    .btn-size-30();
}

.button-size(@height, @top, @left-right, @bottom,
        @font-size: @btn-font-size, @border-radius: @btn-border-radius) {

    padding: @top @left-right @bottom;
    font-size: @font-size;
    border-radius: @border-radius;
    height: @height + 2;
    line-height: @height - @top - @bottom;

    @min: min(@left-right, @btn-padding-left);

    &.btn-dropdown,
    &.btn-modal,
    &.btn-select {
        padding-right: @btn-padding-left + 10 + @min;
    }

    &.btn-dropdown:after,
    &.btn-modal:after,
    &.btn-select:after {
        right: @min;
    }
}
