.modal-wrap {
    .box-sizing(border-box);
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 1500;
    overflow: hidden;
    &.disabled {
        cursor: not-allowed;
    }
}

.modal-content-container {
    display: inline-block;
    vertical-align: middle;
    overflow: hidden;
    padding: 30px 0 0;
}

.modal-container {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    text-align: center;
    white-space: nowrap;
    overflow-y: auto;
    &:before {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
    }
}

.modal-window {
    display: none !important;
    position: relative;
    background: @modal-base-bg;
    text-align: left;
    outline: 0;
    padding-top: @modal-padding-top;
    white-space: normal;
    cursor: auto;
    .box-shadow(0 3px 9px rgba(0,0,0,.5));
    .clearfix();
    .modal-sticky & {
        padding-bottom: 80px;
    }
}

.modal-shown {
    display: block !important;
}

.modal-header {
    .box-sizing(border-box);
    .h3;
    padding: 0 (@modal-padding-left + 25px) 0 @modal-padding-left;
    margin: 0 0 @modal-padding-top;
    line-height: 25px;

    &.modal-header-border {
        border-bottom: 1px solid @modal-hr-color;
        padding-bottom: @modal-padding-top;
    }
}

.modal-content {
    .box-sizing(border-box);
    padding: 0 @modal-padding-left;
    margin: 0 0 @modal-padding-top;
    outline: 0;
}

.modal-content-inner {
    margin: 0 0 @margin-paragraph; 
    overflow: hidden;
    text-overflow: ellipsis;
}

.modal-footer {
    .box-sizing(border-box);
    padding: 20px @modal-padding-left;
    border-top: 1px solid @modal-hr-color;
    white-space: nowrap;
    vertical-align: middle;
    background-color: @modal-footer-bg;

    .modal-sticky & {
        position: fixed;
        bottom: 0;
        box-shadow: 0 -3px 12px rgba(0, 0, 0, .2);
    }

    .btn + .link, .link + .link {
        margin-left: 8px;
    }
}

.modal-close {
    .box-sizing(border-box);
    position: absolute;
    z-index: 1;
    top: 10px;
    right: 18px;
    font-size: 28px;
    line-height: 28px;
    cursor: pointer;
    height: 19px;
    width: 19px;
    color: @modal-close-color;
    &:hover {
        color: @modal-close-color-hover;
    }
}

.modal-scrollbar-measure {
    position: absolute;
    top: -9999px;
    width: 50px;
    height: 50px;
    overflow: scroll;
}

.modal-open {
    overflow: hidden; 
}

.modal-backdrop {
    .box-sizing(border-box);
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1000;
    background-color: @modal-backdrop-bg;
    .opacity(@modal-backdrop-opacity);
}

.modal-expand {
    margin: 0 0 @margin-paragraph;
}
