.btn-base-color() {
  color: @text-color-base;
  background-color: @btn-base-bg;
  .linear-gradient(@btn-base-bg; @btn-base-bg-bottom);
  .border-color(@btn-base-border, @btn-base-border-bottom);

  &:hover,
  &.hover {
    background-color: @btn-base-bg-hover;
    .linear-gradient(@btn-base-bg-hover; @btn-base-bg-bottom-hover);
    .border-color(@btn-base-border-hover, @btn-base-border-bottom-hover);
  }
  &:active,
  &.active {
    background-color: @btn-base-bg-active;
    .linear-gradient(@btn-base-bg-active);
    .border-color(@btn-base-border-active);
  }
}

.btn-disabled() {
  color: @text-color-disabled;
  background: @disabled-color !important;
  //.linear-gradient(@disabled-color);
  .border-color(@border-disabled-color);

  &.checked {
    background: @border-disabled-color !important;
    .border-color(@border-disabled-color);
  }

  &:hover,
  &.hover {
    background-color: @disabled-color;
    .linear-gradient(@disabled-color);
    .border-color(@border-disabled-color);
  }
  &:active,
  &.active {
    background-color: @disabled-color;
    .linear-gradient(@disabled-color);
    .border-color(@border-disabled-color);
  }
}

.btn-checked() {
  color: @text-color-white;
  background-color: @btn-base-bg-checked;
  .linear-gradient(@btn-base-bg-checked);
  .border-color(@btn-base-border-checked);

  &:hover,
  &.hover {
    background-color: @btn-base-bg-checked;
    .linear-gradient(@btn-base-bg-checked);
    .border-color(@btn-base-border-checked);
  }
  &:active,
  &.active {
    background-color: @btn-base-bg-checked;
    .linear-gradient(@btn-base-bg-checked);
    .border-color(@btn-base-border-checked);
  }
}

.linear-gradient(@top) {
  .linear-gradient(@top; @top);
}
.linear-gradient(@top; @bottom) {
  background-image: -webkit-linear-gradient(top, @top, @bottom);
  background-image: -moz-linear-gradient(top, @top, @bottom);
  background-image: -ms-linear-gradient(top, @top, @bottom);
  background-image: linear-gradient(to bottom, @top, @bottom);
}

.border-color(@color) {
  .border-color(@color; @color);
}
.border-color(@top; @bottom) {
  border-top-color: @top;
  border-right-color: @top;
  border-left-color: @top;
  border-bottom-color: @bottom;
}
