.text-light {
  color: @text-color-light;
}
.text-red {
  color: @red-color;
}
.text-yellow {
  color: @yellow-color;
}
.text-green {
  color: @green-color;
}

.text-larger {
  font-size: @font-size-larger;
  line-height: 25px;
}
.text-large {
  font-size: @font-size-large;
  line-height: 20px;
}
.text-normal {
  font-size: @font-size-base;
  line-height: 20px;
}
.text-small {
  font-size: @font-size-small;
  line-height: 15px;
}
.text-code {
  font-family: @font-family-monospace;
}

.text-success {
  .text-icon(@green-color, '\e006');
}
.text-warning {
  .text-icon(@yellow-color, '\e005');
}
.text-error {
  .text-icon(@red-color, '\e004');
}

.text-icon(@color; @icon) {
  color: @color;

  &:before {
    .icon;
    content: @icon;
    padding-right: 3px;
  }
}
