.radio {
    .box-sizing(border-box);
    display: block;
    position: relative;
    max-width: 220px;
    line-height: 16px;
    cursor: pointer;
    font-weight: normal;
    font-size: @font-size-base;
    outline: 0;
    vertical-align: baseline;
    padding: 1px 0 13px 28px;

    &:before {
        .box-sizing(border-box);
        position: absolute;
        height: 18px;
        width: 18px;
        border-radius: 9px;
        line-height: 18px;
        content: "";
        top: 0;
        left: 0;
        border: 1px solid transparent;
    }
    .cbox-base-color();

    &.checked:before {
        .icon;
        text-align: center;
        content: "\e068";
        position: absolute;
        padding: 2px 1.5px;
        line-height: 9px !important;
    }

    &:focus:before,
    &.focus:before {
        outline: none;
        border-radius: 9px;
        //border: 1px solid #ffffff;
        .box-shadow(0 0 0 2px @border-focus-color);
    }

    &.disabled,
    &[disabled],
    fieldset[disabled] & {
        cursor: default;
        pointer-events: none;
        color: #A0A0A0;
        .box-shadow(none);
        .cbox-disabled();
    }
}

.radio > input[type="radio"] {
    .box-sizing(border-box);
    position: absolute;
    top: 0;
    left: 0;
    outline: 0;
    opacity: 0 !important;
    z-index: -1;
}

.radio > label {
    .box-sizing(border-box);
    display: inline-block;
    outline: 0;
    white-space: nowrap;
}
