.tile {
  .box-sizing(border-box);
  padding: 11px 15px;
  min-width: 160px;
  font-size: @font-size-large;
  font-family: @font-family-sans-serif;
  line-height: 20px;
  margin-bottom: 1px;
  margin-right: 20px;
  cursor: pointer;
  vertical-align: top;

  & .tile-legend {
    .box-sizing(border-box);
    font-size: @font-size-base;
  }
}

.tile-main {
  .tile-variant(@text-color-white, @tile-main-bg, @tile-main-bg-hover, @tile-main-bg-active);
  .tile-size(@tile-main-height);
}

.tile-auxiliary {
  .tile-variant(@text-color-white, @tile-sub-bg, @tile-sub-bg-hover, none);
  .tile-size(@tile-sub-height);

}